'use client';

import React, { useEffect, useState, use } from 'react';
import {
    CheckCircle,
    Copy,
    ExternalLink,
    Phone,
    ShieldCheck,
    Timer,
    ArrowLeft,
    Loader2,
    Check,
    Clock,
    AlertCircle,
    CopyCheck
} from 'lucide-react';
import { useRouter } from 'next/navigation';
import { api } from '@/services/api';
import Link from 'next/link';

interface PaymentPageProps {
    params: Promise<{ orderId: string }>;
}

export default function PaymentPage({ params }: PaymentPageProps) {
    const { orderId } = use(params);
    const router = useRouter();
    const [status, setStatus] = useState<'pending' | 'verifying' | 'success' | 'failed'>('pending');
    const [orderData, setOrderData] = useState<any>(null);
    const [loading, setLoading] = useState(true);
    const [copied, setCopied] = useState<string | null>(null);

    useEffect(() => {
        const fetchStatus = async () => {
            try {
                const response = await api.verifyPayment(undefined, orderId);
                setOrderData(response.order);
                if (response.manualStatus) {
                    setStatus(response.manualStatus);
                }
                setLoading(false);
            } catch (error) {
                console.error('Error fetching payment status:', error);
                setLoading(false);
            }
        };

        fetchStatus();
        const interval = setInterval(fetchStatus, 10000); // Poll every 10s
        return () => clearInterval(interval);
    }, [orderId]);

    const copyToClipboard = (text: string, id: string) => {
        navigator.clipboard.writeText(text);
        setCopied(id);
        setTimeout(() => setCopied(null), 2000);
    };

    const [isUploading, setIsUploading] = useState(false);
    const [uploadSuccess, setUploadSuccess] = useState(false);

    const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        setIsUploading(true);
        try {
            await api.uploadProof(orderId as string, file);
            setUploadSuccess(true);
            // Re-fetch status to update UI
            const response = await api.verifyPayment(undefined, orderId as string);
            if (response.manualStatus) setStatus(response.manualStatus);
        } catch (error) {
            console.error('Upload error:', error);
            alert('Erreur lors de l\'envoi de la capture d\'écran. Veuillez réessayer.');
        } finally {
            setIsUploading(false);
        }
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-slate-950 flex flex-col items-center justify-center">
                <Loader2 className="w-12 h-12 text-slate-400 animate-spin mb-4" />
                <p className="text-slate-500 font-bold uppercase tracking-widest text-[10px]">Chargement sécurisé...</p>
            </div>
        );
    }

    const steps = [
        { id: 'pending', label: 'Transfert', icon: Clock, desc: 'En attente' },
        { id: 'verifying', label: 'Vérification', icon: Timer, desc: 'En cours' },
        { id: 'success', label: 'Validé', icon: CheckCircle, desc: 'Prêt' },
    ];

    const currentStepIndex = steps.findIndex(s => s.id === status);

    return (
        <div className="min-h-screen bg-[#0a0a0b] text-white selection:bg-white selection:text-black pb-12 relative z-[9999]">
            {/* Dark Overlay to override any background from layout */}
            <div className="fixed inset-0 bg-[#0a0a0b] z-[-1]" />

            {/* Background Accents */}
            <div className="fixed inset-0 overflow-hidden pointer-events-none z-0">
                <div className="absolute top-[-10%] left-[-10%] w-[40%] h-[40%] bg-slate-900/50 blur-[120px] rounded-full" />
                <div className="absolute bottom-[-10%] right-[-10%] w-[40%] h-[40%] bg-slate-800/30 blur-[120px] rounded-full" />
            </div>

            <div className="max-w-3xl mx-auto px-4 pt-8 md:pt-16 relative z-10">
                {/* Header */}
                <div className="flex items-center justify-between mb-12">
                    <Link href="/" className="p-3 hover:bg-white/5 rounded-full transition-colors group border border-white/5 bg-white/5 backdrop-blur-xl">
                        <ArrowLeft className="w-5 h-5 group-hover:-translate-x-1 transition-transform" />
                    </Link>
                    <div className="text-right">
                        <p className="text-[10px] font-bold uppercase tracking-[0.3em] text-slate-500 mb-1">Réf. TRANSACTION</p>
                        <h2 className="text-lg font-mono font-bold text-white tracking-tight">{orderData?.transactionRef || `#${orderId.slice(0, 8).toUpperCase()}`}</h2>
                    </div>
                </div>

                {/* Status Timeline Card */}
                <div className="bg-white/5 backdrop-blur-3xl rounded-[2.5rem] border border-white/10 p-8 md:p-12 mb-8 shadow-2xl">
                    <div className="relative flex justify-between items-start mb-16">
                        {/* Progress Line */}
                        <div className="absolute top-7 left-0 right-0 h-[2px] bg-white/5 z-0" />
                        <div
                            className="absolute top-7 left-0 h-[2px] bg-white transition-all duration-1000 ease-out z-0"
                            style={{ width: `${(currentStepIndex / (steps.length - 1)) * 100}%` }}
                        />

                        {steps.map((step, idx) => {
                            const isCompleted = idx < currentStepIndex || status === 'success';
                            const isActive = idx === currentStepIndex;
                            const Icon = step.icon;

                            return (
                                <div key={step.id} className="relative z-10 flex flex-col items-center">
                                    <div className={`w-14 h-14 rounded-full flex items-center justify-center transition-all duration-500 ${isCompleted ? 'bg-white text-slate-950 shadow-[0_0_20px_rgba(255,255,255,0.3)]' :
                                        isActive ? 'bg-slate-800 text-white border-2 border-white/20' :
                                            'bg-slate-900 text-slate-600 border border-white/5'
                                        }`}>
                                        {isCompleted && status !== 'failed' ? <Check className="w-6 h-6 stroke-[3px]" /> : <Icon className="w-6 h-6" />}
                                    </div>
                                    <div className="mt-4 text-center">
                                        <p className={`text-[10px] font-bold uppercase tracking-widest ${isActive ? 'text-white' : 'text-slate-500'}`}>{step.label}</p>
                                        <p className={`text-[9px] font-medium uppercase tracking-tighter mt-0.5 ${isActive ? 'text-white/60' : 'text-slate-600'}`}>{step.desc}</p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>

                    <div className="text-center space-y-4">
                        <h1 className="text-3xl md:text-4xl font-bold tracking-tighter">
                            {status === 'pending' && "En attente du transfert"}
                            {status === 'verifying' && "Vérification en cours..."}
                            {status === 'success' && "Paiement Confirmé !"}
                            {status === 'failed' && "Échec de Vérification"}
                        </h1>
                        <p className="text-slate-400 text-lg max-w-md mx-auto leading-relaxed">
                            {status === 'pending' && "Veuillez effectuer le transfert sur l'un des numéros ci-dessous pour valider votre commande."}
                            {status === 'verifying' && "Nos agents vérifient la réception de votre transfert. Cela prend généralement moins de 15 min."}
                            {status === 'success' && "Merci pour votre confiance. Votre commande est désormais en cours de livraison."}
                        </p>
                    </div>

                    {/* Proof Upload Section */}
                    {status === 'pending' && (
                        <div className="mt-12 pt-12 border-t border-white/5">
                            <div className="flex flex-col items-center">
                                <label className="w-full">
                                    <div className={`relative group cursor-pointer overflow-hidden rounded-[2rem] border-2 border-dashed transition-all duration-500 flex flex-col items-center justify-center p-8 gap-4 ${isUploading ? 'border-white/5 bg-white/5 cursor-wait' : 'border-white/10 hover:border-white/30 bg-white/[0.02] hover:bg-white/[0.04]'}`}>
                                        <input
                                            type="file"
                                            className="hidden"
                                            accept="image/*"
                                            onChange={handleFileUpload}
                                            disabled={isUploading}
                                        />

                                        <div className={`w-16 h-16 rounded-2xl flex items-center justify-center transition-all duration-500 ${isUploading ? 'bg-white/10' : 'bg-white/5 group-hover:scale-110 group-hover:rotate-3'}`}>
                                            {isUploading ? (
                                                <Loader2 className="w-8 h-8 text-white animate-spin" />
                                            ) : (
                                                <CopyCheck className="w-8 h-8 text-slate-400 group-hover:text-white" />
                                            )}
                                        </div>

                                        <div className="text-center">
                                            <p className="text-lg font-bold tracking-tight">
                                                {isUploading ? "Envoi du reçu..." : "Envoyer une capture d'écran"}
                                            </p>
                                            <p className="text-sm text-slate-500 mt-1">
                                                Accélérez la validation en envoyant une preuve
                                            </p>
                                        </div>

                                        {/* Success Badge */}
                                        {uploadSuccess && (
                                            <div className="absolute inset-0 bg-green-500 flex flex-col items-center justify-center gap-2 animate-in fade-in duration-500 z-20">
                                                <CheckCircle className="w-12 h-12 text-white" />
                                                <p className="text-xl font-black uppercase tracking-tighter">Reçu envoyé !</p>
                                            </div>
                                        )}
                                    </div>
                                </label>
                            </div>
                        </div>
                    )}
                </div>

                {/* Transfer Details */}
                {status === 'pending' && (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8 animate-in fade-in slide-in-from-bottom-4 duration-700">
                        {/* Numbers Box */}
                        <div className="bg-white/5 backdrop-blur-2xl rounded-[2rem] border border-white/10 p-8">
                            <h3 className="text-xs font-bold uppercase tracking-[0.2em] text-slate-500 mb-6 flex items-center gap-2">
                                <Phone className="w-4 h-4" />
                                Numéros Disponibles
                            </h3>
                            <div className="space-y-4">
                                {[
                                    { label: 'Moov & Wave', number: '+225 01 43 67 83 97' },
                                    { label: 'Orange & Wave', number: '+225 07 07 63 18 61' },
                                    { label: 'MTN & Wave', number: '+225 05 07 85 99 53' }
                                ].map((n, i) => (
                                    <div key={i} className="group flex items-center justify-between p-4 bg-white/[0.03] hover:bg-white/[0.08] rounded-2xl border border-white/5 transition-all">
                                        <div>
                                            <p className="text-[10px] font-bold text-slate-500 uppercase mb-0.5">{n.label}</p>
                                            <p className="text-lg font-mono font-bold tracking-tight">{n.number}</p>
                                        </div>
                                        <button
                                            onClick={() => copyToClipboard(n.number, n.number)}
                                            className="p-3 hover:bg-white/10 rounded-xl transition-colors text-slate-400 hover:text-white"
                                        >
                                            {copied === n.number ? <Check className="w-5 h-5 text-green-400" /> : <Copy className="w-5 h-5" />}
                                        </button>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Amount & Ref Box */}
                        <div className="flex flex-col gap-6">
                            <div className="bg-white/5 backdrop-blur-2xl rounded-[2rem] border border-white/10 p-8 flex-1">
                                <h3 className="text-xs font-bold uppercase tracking-[0.2em] text-slate-500 mb-4">Montant à transférer</h3>
                                <div className="flex items-baseline gap-2 mb-8">
                                    <span className="text-5xl font-bold tracking-tighter">{orderData?.total?.toLocaleString()}</span>
                                    <span className="text-xl font-bold text-slate-500">FCFA</span>
                                </div>
                                <div className="p-4 bg-amber-500/10 border border-amber-500/20 rounded-2xl flex gap-4">
                                    <AlertCircle className="w-6 h-6 text-amber-500 flex-shrink-0" />
                                    <p className="text-xs text-amber-200/80 leading-relaxed italic">
                                        Notez votre référence de commande dans le motif du transfert si possible.
                                    </p>
                                </div>
                            </div>

                            <div className="bg-white/5 backdrop-blur-2xl rounded-[2rem] border border-white/10 p-8 flex items-center justify-between group cursor-pointer hover:bg-white/[0.02] transition-colors"
                                onClick={() => window.open('https://wa.me/2250707631861', '_blank')}>
                                <div className="flex items-center gap-4">
                                    <div className="w-12 h-12 bg-green-500/10 rounded-full flex items-center justify-center border border-green-500/20">
                                        <Phone className="w-6 h-6 text-green-500" />
                                    </div>
                                    <div>
                                        <p className="text-sm font-bold">Assistance Directe</p>
                                        <p className="text-[10px] uppercase font-bold text-slate-500 tracking-wider">Des questions ? Contactez-nous</p>
                                    </div>
                                </div>
                                <ExternalLink className="w-5 h-5 text-slate-600 group-hover:text-white transition-colors" />
                            </div>
                        </div>
                    </div>
                )}

                {/* Secure Tag */}
                <div className="flex items-center justify-center gap-6 mt-12 opacity-40 grayscale">
                    <div className="flex items-center gap-2">
                        <ShieldCheck className="w-4 h-4" />
                        <span className="text-[10px] font-bold uppercase tracking-widest">Paiement Sécurisé</span>
                    </div>
                    <div className="w-1 h-1 bg-slate-700 rounded-full" />
                    <div className="flex items-center gap-2">
                        <CheckCircle className="w-4 h-4" />
                        <span className="text-[10px] font-bold uppercase tracking-widest">Vitalité Garantie</span>
                    </div>
                </div>
            </div>
        </div>
    );
}

